@import url(https://fonts.googleapis.com/css?family=Muli);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
        'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

._100_container {
    height: 100vh;
    min-height: 100vh;
    overflow-y: hidden;
}

._100_burgerIcon {
    position: absolute;
    cursor: pointer;
}

@media (max-width: 960px) {
    ._100_burgerIcon {
        top: 42px;
        top: 37.5px;
        left: 28.5px;
    }
}

@media (max-width: 765px) {
    ._100_burgerIcon {
        top: 31px;
        top: 34px;
    }
}

@media (max-width: 667px) {
    ._100_burgerIcon {
        top: 31px;
        top: 30px;
    }
}

@media (max-width: 570px) {
    ._100_burgerIcon {
        top: 27.5px;
    }
}

@media (max-width: 468px) {
    ._100_burgerIcon {
        top: 24.9px;
        left: 21px;
    }
}

._100_section {
    background-Color: #e1e2e1;
    padding: 10px 30px 0;
}

@media (max-width: 468px) {
    ._100_section {
        padding: 30px 7px 7px;
    }
}

a {
    text-decoration: none;
    cursor: auto;
}

button {
    border: none;
    cursor: pointer;
    color: rgba(0, 0, 0, 0.87);
    display: inline-block;
    text-align: center;
    text-decoration: none;
    padding: 0;
    box-shadow: none;
    min-width: 0;
    box-sizing: border-box;
    min-height: 36px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
        box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-weight: 500;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-style: normal;
    font-size: 0.875rem;
    border-radius: 3%;
    line-height: 0px;
    outline: 0 !important;
}

button:hover {
    background-color: none;
}

._200_mainContainer {
    height: 100%;
    min-height: 100vh;
}

._200_container {
    background-color: #1d2024;
    width: 260px;
    height: 100%;
    box-shadow: 0 10px 30px -12px rgba(0, 0, 0, 0.42), 0 4px 25px 0px rgba(0, 0, 0, 0.12),
        0 8px 10px -5px rgba(0, 0, 0, 0.2);
}

._200_mainContainerMobile {
    overflow: hidden;
}

._200_containerMobile {
    position: absolute;
    z-index: 2;
    overflow: auto;
    padding-bottom: 70px;
    transition: 0.5s cubic-bezier(0.001, 1, 1, 1);
}

._200_hide {
    left: -300px;
}

._200_show {
    left: 0;
}

._200_outsideLayer {
    position: absolute;
    width: 100vw;
    height: 122vh;
    min-width: 100%;
    padding-bottom: 4%;
    background-color: rgba(0, 0, 0, 0.712);
    z-index: 1;
}

@media (max-height: 491px) {
    ._200_outsideLayer {
        height: 207vh;
    }
}

._200_separator {
    border-top: 1px solid #dfe0eb;
    margin-top: 13px;
    margin-bottom: 14px;
    opacity: 0.06;
}

._300_container {
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    padding-top: 25px;
    padding-bottom: 15px;
    background-color: #1d2027;
    z-index: 2;
}

._300_logo {
    background-color: #0856e708;
    padding: 10px 0;
    cursor: pointer;
}

._300_motto {
    margin-bottom: 6px;
    font-family: Times;
    font-style: italic;
    font-size: 19px;
    line-height: 14px;
    letter-spacing: 1px;
    color: #ffffff;
    opacity: 0.9;
}

._300_title {
    margin-left: 8px;
    margin-right: 25px;
    font-family: 'Akronim';
    font-weight: bold;
    font-size: 22px;
    letter-spacing: 0.6px;
    color: #b7f460;
    opacity: 0.9;
}

._300_name {
    color: #b7f460;
    font-size: 15px;
    letter-spacing: 0.25px;
    line-height: 20px;
    opacity: 0.75;
}

._300_separator {
    margin: 7px 20px 12px;
    border-top: 1px solid #dfe0eb;
    opacity: 0.1;
}

._210_activeBar {
    height: 56px;
    width: 3px;
    background-color: #dde2ff;
    position: absolute;
    left: 0;
}

._210_container {
    height: 56px;
    padding-left: 32px;
    padding-right: 32px;
    margin-bottom: 1px;
    cursor: pointer;
}

._210_container:hover {
    background-color: rgba(221, 226, 255, 0.038);
    opacity: 1;
}

@media (max-width: 960px) {
    ._210_container:hover {
        background-color: rgba(221, 226, 255, 0.08);
    }
}

._210_activeContainer {
    background-color: rgba(221, 226, 255, 0.08);
}

@media (max-width: 960px) {
    ._210_activeContainer {
        background-color: rgba(221, 226, 255, 0.038);
    }
}

._210_title {
    font-family: Muli;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.6px;
    color: #dde2ff;
    opacity: 0.9;
    margin-left: 24px;
}

._210_activeTitle {
    color: #dde2ff;
    opacity: 0.6;
}

._400_container {
    height: 40px;
}

@media (max-width: 960px) {
    ._400_container {
        margin-top: 3%;
    }
}

@media (max-width: 468px) {
    ._400_container {
        margin: -10px auto 8px;
    }
}

._400_profile {
    width: 'auto';
    background-color: #c9ccd1;
    border-radius: 24px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
        box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}

._400_profile:hover {
    background-color: #bbc0c5;
}

._400_profileButton {
    z-index: 1;
}

@media (max-width: 468px) {
    ._400_profileButton {
        margin-right: 16px;
    }
}

._400_avatar {
    height: 35px;
    width: 35px;
    border-radius: 50px;
    border: 1px solid #dfe0eb;
}

._400_name {
    margin: 0 8px 0 12px;
    display: inline-block;
    text-align: right;
    text-transform: uppercase;
    letter-spacing: 0.2px;
    border-radius: 50%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

._400_mobileTitle {
    padding-left: 48px;
    margin: auto;
    font-size: 2.0em;
    font-family: Muli;
    font-weight: bold;
    color: #538e00;
}

._1000_mainContainer {
    position: absolute;
    top: -16%;
    bottom: 0;
    left: 0;
    right: 0;
    display: grid;
    justify-content: center;
    align-items: center;
    min-height: 122vh;
    background-color: rgba(0, 0, 0, 0.712);
    z-index: 2;
}

._1000_container {
    position: relative;
    display: inline-block;
    justify-self: center;
    padding: 20px;
    margin: 1rem;
    min-height: 300px;
    min-width: 300px;
    background: #fff;
    border-radius: 2px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    font-family: -apple-system, BlinkMacSystemFont, 'Muli';
    font-style: italic;
    letter-spacing: 1.1px;
}

@media (max-width: 468px) {
    ._1000_container {
        min-width: 0;
    }
}

._1000_content {
    margin: 20px 0 30px 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Times';
}

._1000_button {
    padding: 20px 12px;
    color: rgba(0, 0, 0, 0.78);
    background-color: #a9f543e0;
    border: 0.3mm ridge #48494f66;
    border-radius: 3%;
    box-shadow: 0px 1px 7px rgba(154, 154, 154, 0.1);
    transition: background-color 450ms cubic-bezier(0.2, 0, 0.2, 1) 0ms,
        box-shadow 750ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border 350ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    font-size: 0.95rem;
    font-weight: 700;
    font-style: italic;
}

._1000_button:hover {
    background-color: #9be63aca;
    color: rgba(0, 0, 0, 0.87);
}

._1000_form > span {
    margin: 20px 0;
}

._500_container {
    width: 100%;
    height: calc(100vh - 20%);
    word-wrap: break-word;
    margin-top: 7%;
    background-color: #ffffff;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
    border-radius: 3px;
    box-sizing: border-box;
}

@media (max-width: 960px) {
    ._500_container {
        margin-top: 5%;
        height: calc(100vh - 12.5%);
    }
}

@media (max-width: 468px) {
    ._500_container {
        height: calc(100vh - 7.5%);
    }
}

@media (max-height: 468px) {
    ._500_container {
        height: calc(100vh - 16.5%);
    }
}

._500_text {
    overflow-y: auto;
    padding: 0 15px 35px;
    tab-size: 4;
    -moz-tab-size: 4;
    text-align: left;
    white-space: pre-wrap;
    word-wrap: break-word;
}

._510_container {
    background-color: #84c12d;
    padding: 26px 0;
    margin: -20px 16px 0;
    color: #1d2024;
    border-radius: 1%;
}

@media (max-width: 960px) {
    ._510_container {
        padding: 14px 0;
        margin: -16px 14px 0;
        background-color: #9ac064;
    }
}

._510_title {
    font-family: Muli;
    font-style: normal;
    font-weight: bold;
    font-size: 1.2rem;
    line-height: 1.5rem;
    letter-spacing: 0.8px;
    margin-left: 40px;
    border-radius: 3px;
}

@media (max-width: 960px) {
    ._510_title {
        margin-left: 36px;
    }
}

@media (max-width: 468px) {
    ._510_title {
        font-size: 17px;
        margin: 0 auto;
        padding-left: 10px;
    }
}

._510_iconStyles {
    cursor: pointer;
    margin-left: 20px;
    margin-right: 64px;
}

@media (max-width: 960px) {
    ._510_iconStyles {
        margin-right: 32px;
    }
}

@media (max-width: 468px) {
    ._510_iconStyles {
        margin-left: -10px;
        margin-right: 12px;
    }
}

.search {
    padding: 4px 15px;
    border: green solid 1px;
    border-radius: 50px;
    outline: none;
}

